<template>
  <md-layout md-column v-on:click="onClick" >
    <toolbar></toolbar>
    <note-form :notes="notes" :on_edit="on_edit"></note-form>
    <md-layout>
      <notes :notes="notes"></notes>
    </md-layout>
  </md-layout>
</template>

<script>
  import toolbar from './components/toolbar'
  import notes from './components/notes'
  import noteForm from './components/noteForm'

  export default {
    name: 'app',
    data () {
      return {
        notes: [
          {
            id: "1",
            type: "ordinary",
            title: "for lunch",
            content: "晚上7点去吃饭",
            images: ["/images/lunch.png"]
          },
          {
            id: "2",
            type: "list",
            lists: ["起床", "刷牙", "吃饭"],
            title: "for breakfast",
            content: "早上7点去吃早饭",
            images: []
          }
        ],
        newNote: {},
        on_edit: false
      }
    },
    components: {
      'toolbar': toolbar,
      'noteForm': noteForm,
      'notes': notes
    },
    methods: {
      onClick() {
          this.on_edit = false;
      }
    },
    mounted() {

    }
  }
</script>

<style scoped>
</style>



